body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.post-details {
  border: 0.5px solid black;
  border-radius: 10px;
  width: 50%;
  margin: 10px auto;
}
.w-100 {
  width: 100%;
  display: flex;
}
.w-45 {
  width: 45%;
}
.p-2 {
  padding: 2px;
}
.p-20 {
  padding-left: 20px;
}
.p-20right {
  padding-right: 20px;
}
.floatright {
  float: right;
}
.floatleftt {
  float: left;
}
.inline-block {
  display: block;
}
.name-location {
  display: flex;
  margin: 10px 0;
}
.w-50 {
  width: 50%;
}
.bold {
  font-weight: bold;
}
.p-10 {
  padding-left: 10px;
}
.flex {
  display: flex;
  flex-direction: column;
}
.inline {
  display: inline;
}
.p-left20 {
  padding: 20px;
}

.Instaclone {
  color: green;
  text-decoration: none;
  font-size: larger;
}
